.container {
    padding: 20px 15px;
}

.inputGroup {
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.input {
    width: 270px;
    padding: 7px;
    margin-bottom: 20px;
    background-color: #fff;
    outline-color: rgba(205, 43, 226, 0.6);

    border-radius: 5px;

    /* @media screen and (min-width: 800px) {
        width: 270px;
    } */

    @media screen and (min-width: 880px) {
        width: 370px;
    }
}

.title{
    font-weight: 600;
    text-align: center;
}

.item{
    display: flex;
    gap: 5px;
    align-items: center;
}

.btn{
    padding: 3px 5px;
    background-color: #fff;
    border: 1px solid gray;
    border-radius: 5px;
    cursor: pointer;
}